<template>
  <div >
    <div id="slogan" class="text-center">
      <h1 >Gerador de nomes</h1>
      <br>
      <h6 class="text-secondary">To fazendo saporra</h6>
    </div>
    <div id="main">
      <DomainList></DomainList>
    </div>
  </div>
</template>
<script>
import "bootstrap/dist/css/bootstrap.css";
import DomainList from "./components/DomainList";
export default {
 

	name: "App",
	components:{
		DomainList
	},
	data:function(){
		return{
			prefixes:["Air","Jet" , "Fligt"],
			sufixes:["Hub" , "Mart"],
	
		};
	},
	methods:{
		addPrefix(prefix){
			this.prefixes.push(prefix);
			this.generate();
		},
		addSufix(sufix){
			this.sufixes.push(sufix);
			this.generate();
		},
		
		deletePrefix(prefix){
			this.prefixes.splice(this.prefixes.indexOf(prefix),1);
			this.generate();

		},
		deleteSufix(sufix){
			this.sufixes.splice(this.sufixes.indexOf(sufix),1);
			this.generate();

		}
	},
	computed:{
		domains(){
			const domains=[];
			for(const prefix of this.prefixes){
				for(const sufix of this.sufixes){
					const name = prefix + sufix;
					const url = name.toUpperCase();
					const checkout = `https://checkout.hostgator.com.br/?a=add&sld=${url}&tld=.com.br`;
					domains.push({
						name,
						checkout
					});
				}
			}
			return domains;
		},
	},

  

};
</script>

<style>
  #sloga{
    margin-bottom: 30px;
    margin-top: 30px;
  }
  #main{
    background-color: #f1f1f1;
    padding-top: 30px;
    padding-bottom: 30px;
  }
