<template>
  <div >
    <div id="main">
      <div class="container">
        <div class="row">
          <div class="col-md">
            <AppItemList title="Prefixo" v-bind:items="prefixes" v-on:addItem="addPrefix" v-on:deleteItem="deletePrefix"></AppItemList>

          </div>
          <div class="col-md">
            <AppItemList title="Sufixo" v-bind:items="sufixes" v-on:addItem="addSufix" v-on:deleteItem="deleteSufix"></AppItemList>
          </div>
        </div>
        <br>
        <h5>Dominios <span class="badge badge-info">{{domains.length}}</span></h5>
        <div class="card">
          <div class="card-body">
            <ul class="list-group">
              <li class="list-group-item" v-for="domain in domains" v-bind:key="domain.name">
                <div class="row">
                  <div class="col-md">
                    {{domain.name}}
                  </div>
                  <div class="col-md text-right">
                    <a class="btn btn-info " target="_blank" v-bind:href="domain.checkout">
                      registrar
                    </a>
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import "bootstrap/dist/css/bootstrap.css";
import AppItemList from "./AppItemList";
export default {
 

	name: "App",
	components:{
		AppItemList
	},
	data:function(){
		return{
			prefixes:["Air","Jet" , "Fligt"],
			sufixes:["Hub" , "Mart"],
	
		};
	},
	methods:{
		addPrefix(prefix){
			this.prefixes.push(prefix);
			this.generate();
		},
		addSufix(sufix){
			this.sufixes.push(sufix);
			this.generate();
		},
		
		deletePrefix(prefix){
			this.prefixes.splice(this.prefixes.indexOf(prefix),1);
			this.generate();

		},
		deleteSufix(sufix){
			this.sufixes.splice(this.sufixes.indexOf(sufix),1);
			this.generate();

		}
	},
	computed:{
		domains(){
			const domains=[];
			for(const prefix of this.prefixes){
				for(const sufix of this.sufixes){
					const name = prefix + sufix;
					const url = name.toUpperCase();
					const checkout = `https://checkout.hostgator.com.br/?a=add&sld=${url}&tld=.com.br`;
					domains.push({
						name,
						checkout
					});
				}
			}
			return domains;
		},
	},

  

};
</script>

